import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { setAddress } from "../action/apartmentAction";
import DaumPostcode from "react-daum-postcode";
import Modal from "react-modal";

const Rag04 = () => {
  const [zipCode, setZipcode] = useState("");
  const [roadAddress, setRoadAddress] = useState("");
  const [detailAddress, setDetailAddress] = useState("");
  const [isOpen, setIsOpen] = useState(false);
  const dispatch = useDispatch();

  const completeHandler = (data) => {
    setZipcode(data.zonecode);
    setRoadAddress(data.roadAddress);
    setIsOpen(false);
  };
  const customStyles = {
    overlay: {
      backgroundColor: "rgba(0,0,0,0.5)",
    },
    content: {
      left: "0",
      margin: "auto",
      width: "500px",
      height: "600px",
      padding: "0",
      overflow: "hidden",
    },
  };

  const toggle = () => {
    setIsOpen(!isOpen);
  };

  const changeHandler = (e) => {
    setDetailAddress(e.target.value);
  }; 
  const handleDetailAddressChange = (event) => {
    setDetailAddress(event.target.value);
  };

  const clickHandler = () => {
    if (detailAddress === "") {
      alert("  .");
    } else {
      /* console.log(zipCode, roadAddress, detailAddress);
      dispatch(setAddress(roadAddress)); // setAddress  
      dispatch(setDetailAddress(detailAddress)); // setDetailAddress   */
      const fullAddress ={
        zipCode,
        roadAddress,
        detailAddress,
      }
      dispatch(setAddress(fullAddress));
    }
  };

  return (
    <div>
      <h1>Rag04</h1>
      <input value={zipCode} readOnly placeholder="" />
      <button onClick={toggle}> </button>
      <br />
      <input value={roadAddress} readOnly placeholder=" " />
      <br />
      <Modal isOpen={isOpen} ariaHideApp={false} style={customStyles}>
        <DaumPostcode onComplete={completeHandler} height="100%" />
      </Modal>
      <input
        type="text"
        onChange={handleDetailAddressChange}
        value={detailAddress}
        placeholder=""
      />
      <br />
      <button onClick={clickHandler}></button>
    </div>
  );
};

export default Rag04;
