import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { saveIntroduction } from "../action/apartmentAction";

const Rag15 = () => {
  const [introduction, setIntroduction]= useState();
  const dispatch = useDispatch();
  //       
  const handleTextChange = (event) => {
    const newIntroduction = event.target.value;
    setIntroduction(newIntroduction);
  };
  const handleSaveIntroduction =()=>{
    dispatch(saveIntroduction(introduction))
  }

  return (
    <div className="ragBody">
      <h1>Rag15</h1>
      <h1> </h1>
      <textarea
        id="lastInformation"
        placeholder="      ."
        value={introduction}
        onChange={handleTextChange}>
        </textarea>
        <button
          className="save-introduction-button"
          onClick={handleSaveIntroduction}>
          
        </button>
    </div>
  );
};

export default Rag15;
