import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { savePropertyDetail } from "../action/apartmentAction";

const Rag11 = () => {
  const dispatch = useDispatch();
  const { loan, pet, parking } = useSelector(
    (state) => state.apartment.propertyDetail
  );

  //     
  const [updatedLoan, setUpdatedLoan] = useState(loan);
  const [updatedPet, setUpdatedPet] = useState(pet);
  const [updatedParking, setUpdatedParking] = useState(parking);

  //    
  const handleOptionChange = (optionType, optionValue) => {
    if (optionType === "loan") {
      setUpdatedLoan(optionValue);
    } else if (optionType === "pet") {
      setUpdatedPet(optionValue);
    } else if (optionType === "parking") {
      setUpdatedParking(optionValue);
    }
  };

  //     
  const handleSaveChanges = () => {
    //   Redux store 
    const updatedPropertyDetail = {
      loan: updatedLoan,
      pet: updatedPet,
      parking: updatedParking,
    };
    dispatch(savePropertyDetail(updatedPropertyDetail));
  };

  return (
    <div className="ragBody">
      <h1>Rag11</h1>
      <div className="category_pick">
        <ul>
          <div className="category_pick">
            <li>
              <div className="loan_posAndimpos">
                <h4></h4>
                <label>
                  <input
                    type="radio"
                    value=""
                    checked={loan === ""}
                    onChange={() => handleOptionChange("loan", "")}
                  />
                  
                </label>
                <label>
                  <input
                    type="radio"
                    value=""
                    checked={loan === ""}
                    onChange={() => handleOptionChange("loan", "")}
                  />
                  
                </label>
                <label>
                  <input
                    type="radio"
                    value=" "
                    checked={loan === " "}
                    onChange={() => handleOptionChange("loan", " ")}
                  />
                   
                </label>
              </div>
            </li>
          </div>
          <div className="category_pick">
            <li>
              <div className="pet_posAndimpos">
                <h4></h4>
                <label>
                  <input
                    type="radio"
                    value=""
                    checked={pet === ""}
                    onChange={() => handleOptionChange("pet", "")}
                  />
                  
                </label>
                <label>
                  <input
                    type="radio"
                    value=""
                    checked={pet === ""}
                    onChange={() => handleOptionChange("pet", "")}
                  />
                  
                </label>
                <label>
                  <input
                    type="radio"
                    value=" "
                    checked={pet === " "}
                    onChange={() => handleOptionChange("pet", " ")}
                  />
                   
                </label>
              </div>
            </li>
          </div>
          <div className="category_pick">
            <li>
              <div className="parking_posAndimpos">
                <h4></h4>
                <label>
                  <input
                    type="radio"
                    value=""
                    checked={parking === ""}
                    onChange={() => handleOptionChange("parking", "")}
                  />
                  
                </label>
                <label>
                  <input
                    type="radio"
                    value=""
                    checked={parking === ""}
                    onChange={() => handleOptionChange("parking", "")}
                  />
                  
                </label>
                <label>
                  <input
                    type="radio"
                    value=" "
                    checked={parking === " "}
                    onChange={() => handleOptionChange("parking", " ")}
                  />
                   
                </label>
              </div>
            </li>
          </div>
        </ul>
        <button onClick={handleSaveChanges}> </button>
      </div>
    </div>
  );
};

export default Rag11;
