export const setUserType = (selectedUserType) => async (dispatch) => {
  try {
    dispatch({ type: types.SET_USER_TYPE, payload: selectedUserType });
    const response = await api.post("/apartmentregister", {
      userType: selectedUserType,
    });
    if (response.status !== 200) throw new Error(response.error);
    dispatch({
      type: types.SET_USER_TYPE_SUCCESS,
      payload: response.data.userType,
    });
    dispatch(commonUiActions.showToastMessage("  ", "success"));
  } catch (error) {
    dispatch({ type: types.SET_USER_TYPE_FAIL, payload: error.error });
    dispatch(commonUiActions.showToastMessage(error.error, "error"));
  }
};
// 
export const setInternalSize = (internalSize) => async (dispatch) => {
  try {
    dispatch({ type: types.SET_INTERNAL_SIZE, payload: internalSize });

    // Send a POST request to save the selected size
    const response = await api.post("/apartmentregister", {
      // /internal-size  
      internalSize,
    });

    if (response.status !== 200) throw new Error(response.error);

    dispatch({
      type: types.SET_INTERNAL_SIZE_SUCCESS,
      payload: response.data.apartment,
    });

    dispatch(
      commonUiActions.showToastMessage("Apartment size saved", "success")
    );
  } catch (error) {
    dispatch({ type: types.SET_INTERNAL_SIZE_FAIL, payload: error.error });

    // Display an error message.
    dispatch(commonUiActions.showToastMessage(error.error, "error"));
  }
};
// 
export const setAddress = (setAddress) => async (dispatch) => {
  try {
    dispatch({ type: types.SET_ADDRESS, payload: setAddress });
    const response = await api.post("/apartmentregister", {
      address: setAddress,
    });
    if (response.status !== 200) throw new Error(response.error);
    const apartmentData = response.data.apartment;
    dispatch({ type: types.SET_ADDRESS_SUCCESS, payload: apartmentData });
    dispatch(
      commonUiActions.showToastMessage(
        "  ",
        "success"
      )
    );
  } catch (error) {
    dispatch({ type: types.SET_ADDRESS_FAIL, payload: error.error });
    dispatch(commonUiActions.showToastMessage(error.error, "error"));
  }
};

