export const saveDescription = (description) => async (dispatch) => {
  try {
    dispatch({ type: types.SET_DESCRIPTION, payload: description });

    const response = await api.post("/apartmentregister", {
      //   /description
      description, //     API    
    });

    if (response.status !== 200) throw new Error(response.error);

    dispatch({
      type: types.SET_DESCRIPTION_SUCCESS,
      payload: response.data.apartment,
    });

    dispatch(
      commonUiActions.showToastMessage(
        "Description saved successfully",
        "success"
      )
    );
  } catch (error) {
    dispatch({ type: types.SET_DESCRIPTION_FAIL, payload: error.error });
    dispatch(commonUiActions.showToastMessage(error.error, "error"));
  }
};
