import React, { useState } from "react";
import ToggleButton from "react-bootstrap/ToggleButton";
import ToggleButtonGroup from "react-bootstrap/ToggleButtonGroup";
import { useDispatch } from "react-redux";
import { savePropertyFeatures } from "../action/apartmentAction";

const Rag14 = () => {
  const [selectvalue, setSelectValue] = useState(); //    
  const dispatch = useDispatch();

  const handleChange = (value) => {
    setSelectValue(value);
    const selectPropertyFeatures = { value };
    dispatch(savePropertyFeatures(selectPropertyFeatures));
  };

  return (
    <div className="ragBody">
      <h1>Rag14</h1>
      <div className="item-container">
        <h2>  </h2>
        <ToggleButtonGroup
          type="checkbox"
          value={selectvalue}
          onChange={handleChange}
          vertical>
          <ToggleButton id="tbg-btn-1" value="transportation">
             
          </ToggleButton>

          <ToggleButton id="tbg-btn-2" value="view">
             
          </ToggleButton>

          <ToggleButton id="tbg-btn-3" value="terrace">
             
          </ToggleButton>

          <ToggleButton id="tbg-btn-4" value="soundproofing">
             
          </ToggleButton>

          <ToggleButton id="tbg-btn-5" value="amenities">
              
          </ToggleButton>

          <ToggleButton id="tbg-btn-6" value="cleanliness">
             
          </ToggleButton>
        </ToggleButtonGroup>
      </div>
    </div>
  );
};

export default Rag14;
