// Rag05.js
import React, { useState, useEffect } from "react"; // useState  useEffect 
import { useDispatch, useSelector } from "react-redux";
import { saveDescription, getDescription } from "../action/apartmentAction";

const Rag05 = () => {
  const dispatch = useDispatch();
  const aptLocation = useSelector((state) => state.apartment?.location || "");
  const aptDescription = useSelector(
    (state) => state.apartment?.description || ""
  ); //   
  const [inputValue, setInputValue] = useState(aptLocation); //       
  const [descriptionInput, setDescriptionInput] = useState(""); //    

  useEffect(() => {
    //      .
    dispatch(getDescription());
  }, [dispatch]);

  const handleLocationChange = (event) => {
    const location = event.target.value;

    //    
    setInputValue(location);
  };

  const handleDescriptionChange = (event) => {
    const description = event.target.value;

    //    
    setDescriptionInput(description);
  };

  const handleSaveDescription = () => {
    //     
    dispatch(saveDescription(descriptionInput));
  };

  return (
    <div className="ragBody">
      <h1>Rag05</h1>
      <div className="hom_rgt01_title_space">
        <div className="main_title">
          <h1>  </h1>
        </div>
      </div>

      <div className="description-container">
        <h2> </h2>
        <textarea
          className="description-textarea"
          placeholder="   ."
          value={descriptionInput}
          onChange={handleDescriptionChange}
        />
        <button
          className="save-description-button"
          onClick={handleSaveDescription}>
          
        </button>
        <p className="saved-description">{aptDescription}</p>
      </div>
    </div>
  );
};

export default Rag05;
