import React, { useState } from "react";
import Form from "react-bootstrap/Form";
import { useDispatch } from "react-redux";
import { saveInteriorOption } from "../action/apartmentAction";

const Rag13 = () => {
  const [selectedOptions, setSelectedOptions] = useState([]);
  const dispatch = useDispatch();

  const handleCheckboxChange = (option) => {
    if (selectedOptions.includes(option)) {
      setSelectedOptions(selectedOptions.filter((item) => item !== option));
    } else {
      setSelectedOptions([...selectedOptions, option]);
    }
  };

  const handleSaveOptions = () => {
    dispatch(saveInteriorOption(selectedOptions));
  };

  return (
    <div className="ragBody">
      <h1>Rag13</h1>
      <div className="main-01">
        <h2>  </h2>
        <Form>
          <div className="itemList">
            <Form.Check
              type="checkbox"
              id={`check-api-aircon`}
              onChange={() => handleCheckboxChange("")}
              checked={selectedOptions.includes("")}
              label=""
            />
            <Form.Check
              type="checkbox"
              id={`check-api-refrigerator`}
              onChange={() => handleCheckboxChange("")}
              checked={selectedOptions.includes("")}
              label=""
            />
            <Form.Check
              type="checkbox"
              id={`check-api-kimchiRefrigerator`}
              onChange={() => handleCheckboxChange("")}
              checked={selectedOptions.includes("")}
              label=""
            />
            <Form.Check
              type="checkbox"
              id={`check-api-washingMachine`}
              onChange={() => handleCheckboxChange("")}
              checked={selectedOptions.includes("")}
              label=""
            />
            <Form.Check
              type="checkbox"
              id={`check-api-microwave`}
              onChange={() => handleCheckboxChange("")}
              checked={selectedOptions.includes("")}
              label=""
            />
            <Form.Check
              type="checkbox"
              id={`check-api-stove`}
              onChange={() => handleCheckboxChange("")}
              checked={selectedOptions.includes("")}
              label=""
            />
            <Form.Check
              type="checkbox"
              id={`check-api-electricRange`}
              onChange={() => handleCheckboxChange("(, )")}
              checked={selectedOptions.includes("(, )")}
              label="(, )"
            />
            {/* <Form.Check
              type="checkbox"
              id={`check-api-electricRange_hiLight`}
              onChange={() => handleCheckboxChange("()")}
              checked={selectedOptions.includes("()")}
              label="()"
            />
            <Form.Check
              type="checkbox"
              id={`check-api-electricRange_induction`}
              onChange={() => handleCheckboxChange("()")}
              checked={selectedOptions.includes("()")}
              label="()"
            /> */}
            <Form.Check
              type="checkbox"
              id={`check-api-rooftop`}
              onChange={() => handleCheckboxChange("")}
              checked={selectedOptions.includes("")}
              label=""
            />
            <Form.Check
              type="checkbox"
              id={`check-api-duplex`}
              onChange={() => handleCheckboxChange("")}
              checked={selectedOptions.includes("")}
              label=""
            />
            <Form.Check
              type="checkbox"
              id={`check-api-elevator`}
              onChange={() => handleCheckboxChange("")}
              checked={selectedOptions.includes("")}
              label=""
            />
            {/* Add more checkboxes with unique IDs and labels */}
          </div>
        </Form>
        <button onClick={handleSaveOptions}></button>
      </div>
    </div>
  );
};

export default Rag13;
